<template>
  <div>
    <div class="topTag">
      <el-tag type="success" class="nameTag">居某</el-tag>
      <el-button :plain="true" @click="open2"  class="btnTag" type="warning">{{ text }}</el-button>
      <el-button @click="clearFilter" class="btnTag">清除全部</el-button>
    </div>
    <div>
      <el-table :data="last" stripe style="width: 80%" height="500" ref="filterTable">
        <el-table-column prop="pool" label="卡池" :filters="poolList" :filter-method="filterHandler" width="200">
          <template slot-scope="scope">
            <el-tag :type="scope.row.tag % 2 === 0 ? 'info' : 'warning'" disable-transitions>{{
              scope.row.pool
            }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="charName" label="名字" width="170">
        </el-table-column>
        <el-table-column prop="rarity" label="星级" width="180" :filters="rarityList" :filter-method="filterHandler">
          <template slot-scope="scope">
            <el-tag :type="scope.row.rarity === '5' ? 'success' : 'danger'" disable-transitions>{{
              scope.row.rarity
            }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="takeOut" label="抽数">
        </el-table-column>
        <el-table-column prop="index6" label="该池已垫">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<style>
.topTag {
  margin-bottom: 30px;
  display: flex;
}

.nameTag {
  margin: 0 10px;
  justify-content: flex-start;
}

.btnTag {
  font-size: 7px;
  line-height: 7px;
  width: 100px;
  height: 30px;
  margin-left: 10px;
}
</style>

<script>
import urlList from '../loginurl/url'
export default {
  data()
  {
    return {
      text:'点击获取',
      page: 1,
      cardList: [],
      url:urlList.data.dogUrl,
      cardName: [],
      last: [],
      rarityList: [{ text: '5', value: '5' }, { text: '6', value: '6' },],
      poolList: [],
    }
  },

  methods: {
    open2(){
      this.$message({
        message: '恭喜你触发了隐藏特效,获得至尊狗头衔!',
        type: 'warning'
      });
      this.text = '至臻狗';
    },
    clearFilter()
    {
      this.$refs.filterTable.clearFilter();
      this.text = '点击获取'
    },
    get()
    {
      let that = this;
      for (let i = 1; i < 11; i++)
      {
        this.axios.get('/api' + '?page=' + i + this.url)
          .then(function (response)
          {
            if (response.data.data.list.length > 0)
            {
              let data = response.data.data;
              that.cardList[data.pagination.current - 1] = data.list;
            } else
            {
              return;
            }
          }, function (err)
          {
            console.log('err', err)
          })
      }
      console.log('完成了get调用!');
      setTimeout(() =>
      {
        this.fenxi();
        console.log('完成了fenxi调用!');
      }, 1500);
      clearTimeout();
    },
    fenxi()
    {
      //这里是新的分析方法
      this.cardList = [].concat(...this.cardList);
      //将数据解构成数组
      let utext = [];
      for (let i in this.cardList)
      {
        if (this.cardList[i].chars.length == 10)
        {
          for (let j = 0; j < 10; j++)
          {
            utext.push({
              ts: this.cardList[i].ts,
              pool: this.cardList[i].pool,
              chars: [this.cardList[i].chars[j]]
            })
          }
        } else
        {
          utext.push({
            ts: this.cardList[i].ts,
            pool: this.cardList[i].pool,
            chars: [this.cardList[i].chars[0]]
          });
        }
      }
      utext = utext.reverse();
      let timeLast = utext[utext.length - 1].ts;
      //提取卡池名
      let cardName = [...new Set(utext.map(i => i.pool))];
      this.poolList = cardName.map(item =>
      {
        return { text: item, value: item }
      })
      //console.log(this.cardName)
      //分别存储各卡池的记录
      utext = cardName.map(i =>
        utext.filter(j =>
          j.pool == i
        )
      ).map(i =>
        i.map(j => j.chars)
      );
      //第二轮筛选 五星和六星的抽数
      for (let i = 0; i < utext.length; i++)
      {
        let index5 = 0;
        let index6 = 0;
        let p5 = [];
        let p6 = [];
        for (let j = 0; j < utext[i].length; j++)
        {
          if (utext[i][j][0].rarity == 4)
          {
            index5 += 1;
            p5.push(utext[i][j][0]['name'] + ',' + index5);
            index6++;
            index5 = 0;
          } else if (utext[i][j][0].rarity == 5)
          {
            index6 += 1;
            p6.push(utext[i][j][0]['name'] + ',' + index6);
            index5++;
            index6 = 0;
          } else
          {
            index5++;
            index6++;
          }
        }
        //合并数据      
        p5.map(item =>
        {
          this.last.push({
            pool: this.poolList[i].text,
            charName: item.split(',')[0],
            takeOut: item.split(',')[1],
            rarity: '5',
            tag: i,
          });
        });
        p6.map(item =>
        {
          this.last.push({
            pool: this.poolList[i].text,
            charName: item.split(',')[0],
            takeOut: item.split(',')[1],
            rarity: '6',
            tag: i,
            //index6:index6,
          });
        }
        );
        this.last[this.last.length - 1].index6 = index6;
        //清空数据
        p5, p6 = [];
        console.log(this.last)
      }
      console.log('完成了!')
    },
    filterHandler(value, row, column)
    {
      //console.log(value)
      const property = column['property'];
      return row[property] === value;
    }

  },
  //初始化时调用函数
  mounted()
  {
    this.get();
    console.log(this.last)
  }
}
</script>
